import React from 'react';

const Books = (props) => {

    return(
        <div className='main'>
            {props.books.map((el, i) => {
                const book = el.volumeInfo;
                return (
                        <li key={el.id} className='thumbnail'>
                            <img src={book.imageLinks.smallThumbnail} alt='book_cover' className='thumbnail_img'/>
                            <ul className='book_info'>
                                <li className='book_title'>{book.title}</li>
                                <li>By: {book.publisher}</li>
                                <li>Published by: {book.authors}</li>
                                <li className='see_book'><a href={book.previewLink} target='blank'>SEE BOOK</a></li>
                            </ul>
                        </li>
                )
            })}
        </div>
    )

}

export default Books;