import React, { Component } from 'react';
import Searchbar from './Searchbar';
import Books from './Books';
import request from 'superagent';


class App extends Component {
  constructor(props){
    super(props);

    this.state = {
      query: '',
      books: null
    }

    this.onQueryChange = this.onQueryChange.bind(this);
    this.onFormSubmit = this.onFormSubmit.bind(this);
  }

  onQueryChange(e){
    this.setState({query: e.target.value});
  }
  submitEmptyField(){
    this.setState()
  }
  onFormSubmit(e){
      e.preventDefault();
      const val = this.state.query;
      const url = 'https://www.googleapis.com/books/v1/volumes?q='+val;

      request
          .get(url)
          .then((data) => {
                this.setState(() => {
                return {
                  books: data.body.items
                }
                
            });
          })
          .catch((err) => {
            console.log(err);
          })     
  }

  render() {
      return (
        <div>
            <Searchbar onChange={this.onQueryChange} val={this.state.query} onSubmit={this.onFormSubmit}/>
            {!this.state.books ? <div></div> : <Books books={this.state.books}/>}
        </div>
          
      )
  }
}

export default App;
